import { Component, OnInit, ViewChild, Input, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { AuthService } from '../../../service/auth.service';
import { AccountService } from '../../../service/account.service';
import { FactoryService } from '../../../service/factory.service';

@Component({
  selector: 'app-edit-menu',
  templateUrl: './edit-menu.component.html',
  styleUrls: ['./edit-menu.component.css']
})
export class EditMenuComponent implements OnInit {

  public menu;
  public action;
  
  constructor(public auth: AuthService, public accountService: AccountService, public dialogRef: MatDialogRef<EditMenuComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { 
    this.menu=data.menu;
    this.action=data.action;
  }

  ngOnInit() {
  }
  
  cancel(){
    this.dialogRef.close(false);
  }
  
  save(){
    if(this.action=='update'){
      this.accountService.api_update_menu({'menu':this.menu}).then(res => {
        this.dialogRef.close(true);
      });
    }
    
    if(this.action=='add'){
      this.accountService.api_add_menu({'menu':this.menu}).then(res => {
        this.dialogRef.close(true);
      });
    }
    
  }

}
