import { Component, OnInit, ViewChild, EventEmitter, Input, Output } from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import { SurveyCreateService } from '../../../service/survey-create.service';
import { ActivatedRoute } from '@angular/router';
import { MatHorizontalStepper } from '@angular/material/stepper';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';

@Component({
  selector: 'app-survey-question-create',
  templateUrl: './survey-question-create.component.html',
  styleUrls: ['./survey-question-create.component.css']
})
export class SurveyQuestionCreateComponent implements OnInit {
  @Input() group: FormGroup;
  @Output() groupChange: EventEmitter<FormGroup> = new EventEmitter<FormGroup>();
  question:any={
    SurveyQuestionTypeID:1,
    choices : []
  };
  questionID=0;
  choice_types:any=[];
  text_types:any=[];
  types:any=[];
  type_config:any={};
  config_checked=false;
  choiceControl:any=new FormControl('',[Validators.required]);
  constructor(private _formBuilder: FormBuilder, private scs:SurveyCreateService) { 
    
  }

  bindValue(question){
    this.question=question;
    this.group.get('textCtrl').setValue(question.Text);
    this.group.get('infoCtrl').setValue(question.InfoText);
    this.group.get('typeCtrl').setValue(question.SurveyQuestionTypeID);
    this.group.get('requiredCtrl').setValue(question.IsRequired==1);
    if(question.ChoiceDisplayType==null){
      this.group.get('choiceCtrl').setValue('radio');
    }
    else {
      this.group.get('choiceCtrl').setValue(question.ChoiceDisplayType);
    }
    if(question.TextDataType==null){
      this.group.get('dataCtrl').setValue('text');
    }
    else {
      this.group.get('dataCtrl').setValue(question.TextDataType);
    }
    this.group.get('entryCtrl').setValue(question.DefaultEntryText);
  }
  getChoiceAndTextAndTypes(){
    this.choice_types=this.scs.getChoiceDisplayTypes();
    this.text_types=this.scs.getTextDisplayTypes();
    this.types=this.scs.getQuestionTypes();
    this.type_config=this.scs.getQuestionTypeConfigs();
  }
  ngOnInit() {
    this.scs.configEvent.subscribe(data=>{
      this.getChoiceAndTextAndTypes();
    })
    this.initControl();
    this.scs.questionEvent.subscribe(action=>{
      console.log(action);
      this.bindValue(this.scs.getQuestion());
    });
    this.scs.initQuestion();
    this.getChoiceAndTextAndTypes();
  }

  // 'InfoText'=>true, 
  // 'ChoiceDisplayType'=>true,
  // 'DefaultEntryText'=>'input',
  // 'TextDataType'=>true
  initControl(){
    this.group = this._formBuilder.group({
      textCtrl: ['', Validators.required],
      requiredCtrl:[false,],
      typeCtrl:[1,Validators.required],
      infoCtrl: ['',],
      choiceCtrl:['radio',],
      entryCtrl:['',],
      dataCtrl:['text',]
    });
    this.groupChange.emit(this.group);
  }
  save(){
    if(this.group.invalid){
      this.group.updateValueAndValidity();
      return;
    }
    this.scs.updateQuestion(this.preparequestion());
    this.scs.previous();
  }
  createChoice(){
    if(this.choiceControl.invalid){
      this.choiceControl.updateValueAndValidity();
      return;
    }
    else {
      if(!this.scs.checkCreatedQuestion()){
        this.scs.updateQuestion(this.preparequestion());
      }
      this.scs.createChoice(this.choiceControl.value);
    }
  }
  cancel(){
    this.scs.initQuestion();
    this.scs.previous();
  }
  preparequestion(){
    let question= {
    Text:this.group.get('textCtrl').value,
    InfoText:this.group.get('infoCtrl').value,
    SurveyQuestionTypeID:this.group.get('typeCtrl').value,
    IsRequired:this.group.get('requiredCtrl').value==1,
    ChoiceDisplayType:this.group.get('choiceCtrl').value,
    DefaultEntryText:this.group.get('entryCtrl').value,
    TextDataType:this.group.get('dataCtrl').value,
    }
    return question;
  }

  drop(event: CdkDragDrop<string[]>) {
    this.scs.moveChoice(event.previousIndex, event.currentIndex);
    moveItemInArray(this.question.choices, event.previousIndex, event.currentIndex);
  }
  move(event){
    moveItemInArray(this.question.choices, event.start, event.end);
  }
  changeType($event){
    console.log($event);
    this.question.SurveyQuestionTypeID=$event.value;
  }
}
