import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';
import { EditApplicationComponent } from './edit-application/edit-application.component';

@Component({
  selector: 'app-organization-configuration',
  templateUrl: './organization-configuration.component.html',
  styleUrls: ['./organization-configuration.component.css']
})
export class OrganizationConfigurationComponent implements OnInit {

  public organizationConfiguration;
  public saving;
  public saved;
  
  public applicationID;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService, public dialog: MatDialog) { 
    this.saving=false;
    this.saved=false;
    this.get_organization_configuration();
  }
  
  ngOnInit() {
    
  }

  get_organization_configuration(){
    this.accountService.api_organization_configuration().then(res => {
      this.organizationConfiguration=res;
    });
  }
  
  //
  save_configuration(){
    this.saving=true;
    this.accountService.api_save_organization_configuration(this.organizationConfiguration).then(res => {
      this.saving=false;
      this.get_organization_configuration();
      
      this.saved=true;
      setTimeout(()=>{    
        this.saved=false;
      }, 3000);
      
      this.auth.refresh_userinfo();
    });
  }
  
  save_reminder_for_type(){
    if(confirm("You are about to update the notification and reminder delivery preferences for ALL Students in your organization. Are you sure?")) {
      this.save_configuration();
    }
  }
  
  //
  get_person_type(person_type){
    this.accountService.api_get_person_type(person_type).then(res => {
      this.organizationConfiguration.reminder_for_type=res;
    });
  }
  
  //application
  add_application(){
    this.openDialog('add');
  }
  
  //application
  update_application(applicationID){
    this.applicationID=applicationID;
    this.openDialog('update');
  }
  
  //application
  delete_application(applicationID){
    let application={};
    for(let v of this.organizationConfiguration.applications){
      if(v.ID==applicationID){
        application=v;
      }
    }
    
    this.accountService.api_delete_application({'application':application}).then(res => {
      this.view_application();
    });
  }
  
  //
  openDialog(action){
    let application={};
    
    if(action=='update'){
      for(let v of this.organizationConfiguration.applications){
        if(v.ID==this.applicationID){
          application=v;
        }
      }
    }
    const dialogRef = this.dialog.open(EditApplicationComponent, {
      width: '550px',
      data: {"application":application,'milestoneSelection':this.organizationConfiguration.config.milestone,'action':action}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result){
        this.view_application();
      }
    });
  }
  
  
  //application
  view_application(){
    this.accountService.api_view_application().then(res => {
      this.organizationConfiguration.applications=res;
    });
  }

}


























