import { Component, OnInit, ViewChild } from '@angular/core';
import {MatSort, MatTableDataSource, MatTable} from '@angular/material';
import { Router } from '@angular/router'; 
import { AccountService } from '../../service/account.service';
import {Observable, Observer} from 'rxjs';

@Component({
  selector: 'app-resource',
  templateUrl: './resource.component.html',
  styleUrls: ['./resource.component.css']
})
export class ResourceComponent implements OnInit {
  resources:any={
    owned:[],
    succeeded:[]
  };
  owned : MatTableDataSource<ResourceElement>;
  succeeded : MatTableDataSource<ResourceElement>;
  owned_column:string[]=['Title','Description','URL','Edit'];
  succeeded_column:string[]=['Title','Description','URL', 'Available'];


  @ViewChild('matSort1') sort1: MatSort;
  @ViewChild('matSort2') sort2: MatSort;
  @ViewChild('table1') table1: MatTable<ResourceElement>;
  @ViewChild('table2') table2: MatTable<ResourceElement>;

  constructor(private acc:AccountService) { 
    this.getData();
  }

  getData(){
    this.acc.getResourceList().then(res=>{
      this.owned=new MatTableDataSource<ResourceElement>(res.owned);
      this.succeeded=new MatTableDataSource<ResourceElement>(res.succeeded);
      this.owned.sort=this.sort1;
      this.succeeded.sort=this.sort2;
    });
  }
  ngOnInit() {
  }
  createResource(){
    this.owned.data.push(
      {
        ID:0,
        Title:'',
        Description: '',
        IsActive:1,
        URL:''
      }
    );
    this.owned.data= [...this.owned.data];
    this.table1.renderRows();
  }
  edit(){

  }
  delete(ID){
    this.owned.data=[...this.owned.data.filter(item=>{
      return item.ID!==ID;
    })];
  }
  setAvailable(element,$event){
    console.log($event);
    this.acc.setResourceAvailability(element.ID, $event.checked?1:0);
  }
  saveChange(){
    this.acc.updateResourceList(this.owned.data).then(res=>{
      this.owned.data=res.owned;
    })
  }
  undoChange(){
    this.getData();
  }
}
export interface ResourceElement {
  ID:number;
  Title: string;
  Description: string;
  IsActive:number;
  URL:String;
}