import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';
import { EditCategoryComponent } from './edit-category/edit-category.component';

@Component({
  selector: 'app-student-categories',
  templateUrl: './student-categories.component.html',
  styleUrls: ['./student-categories.component.css']
})
export class StudentCategoriesComponent implements OnInit {

  public user_info;
  public student_group;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService, public dialog: MatDialog) { 
    this.get_student_group();
  }

  ngOnInit() {
    
  }
  
  //
  get_student_group(){
    this.accountService.api_get_student_group_list().then(res => {
      this.student_group=res;
    });
  }

  //
  create_student_group(){
    this.openDialog("create",0);
  }
  
  //
  update_student_group(ID){
    this.openDialog("update",ID);
  }
  
  //
  delete_student_group(ID){
    this.accountService.api_delete_student_group(ID).then(res => {
      this.get_student_group();
    });
  }
  
  openDialog(action,ID){
    const dialogRef = this.dialog.open(EditCategoryComponent, {
      width: '850px',
      data: {'user_info':this.auth.userinfo,'student_group':this.student_group,'action':action,'ID':ID}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result){
        this.get_student_group();
      }
    });
  }
  
  
  
  
  
  
  
  
  
  
  
  
}
