import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';

import { AccountComponent } from './account.component';
import { UserComponent } from '../account/user/user.component';
import { AdvisorInformationComponent } from '../account/advisor-information/advisor-information.component';
import { AdvisorConfigurationComponent } from '../account/advisor-configuration/advisor-configuration.component';
import { OrganizationConfigurationComponent } from '../account/organization-configuration/organization-configuration.component';
import { OrganizationInformationComponent } from '../account/organization-information/organization-information.component';
import { SurveyComponent } from '../account/survey/survey.component';
import { TaggedListComponent } from '../account/tagged-list/tagged-list.component';
import { ResourceComponent } from '../account/resource/resource.component';
import { CollegeConfigurationComponent } from '../account/college-configuration/college-configuration.component';
import { StudentCategoriesComponent } from '../account/student-categories/student-categories.component';
import { CustomMenuComponent } from '../account/custom-menu/custom-menu.component';
import { BillingComponent } from '../account/billing/billing.component';
import { StudentsComponent } from '../account/students/students.component';
import { ProfileComponent } from '../account/profile/profile.component';
import { FamilyComponent } from '../account/profile/family/family.component';
import { StudentComponent } from '../account/profile/student/student.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { SurveyCreateComponent } from './survey/survey-create/survey-create.component';
import { SurveySectionCreateComponent } from './survey/survey-section-create/survey-section-create.component';
import { SurveyQuestionCreateComponent } from './survey/survey-question-create/survey-question-create.component';
import { SurveySectionItemComponent } from './survey/survey-section-item/survey-section-item.component';
import { SurveyQuestionItemComponent } from './survey/survey-question-item/survey-question-item.component';
import { SurveyChoiceItemComponent } from './survey/survey-choice-item/survey-choice-item.component';
import { SurveySectionTemplateComponent } from './survey/survey-section-template/survey-section-template.component';
import { EditApplicationComponent } from './organization-configuration/edit-application/edit-application.component';
import { EditMenuComponent } from './custom-menu/edit-menu/edit-menu.component';
import { CreateUserComponent } from './user/create-user/create-user.component';
import { TaggedListEditComponent } from './tagged-list/tagged-list-edit/tagged-list-edit.component';
import { ManageUserComponent } from './user/manage-user/manage-user.component';
import { StorageComponent } from './storage/storage.component';
import { EditCategoryComponent } from './student-categories/edit-category/edit-category.component';
import { MenuComponent } from './menu/menu.component';

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: AccountComponent,
        children: [
          { path: '',component:UserComponent },
          { path: 'user', component: UserComponent },
          { path: 'AdvisorInformation', component: AdvisorInformationComponent },
          { path: 'AdvisorConfiguration', component: AdvisorConfigurationComponent },
          { path: 'OrganizationConfiguration', component: OrganizationConfigurationComponent },
          { path: 'OrganizationInformation', component: OrganizationInformationComponent },
          { path: 'survey', component: SurveyComponent }, 
          { path: 'survey/create', component: SurveyCreateComponent,
          data: { type: 'create' } }, 
          { path: 'survey/edit/:id', component: SurveyCreateComponent,
          data: { type: 'edit' } },
          { path: 'TaggedList/create', component: TaggedListEditComponent,
          data: { type: 'create' } }, 
          { path: 'TaggedList/edit/:id', component: TaggedListEditComponent,
          data: { type: 'edit' } }, 
          { path: 'TaggedList', component: TaggedListComponent },
          { path: 'resource', component: ResourceComponent },
          { path: 'CollegeConfiguration', component: CollegeConfigurationComponent },
          { path: 'StudentCategories', component: StudentCategoriesComponent },
          { path: 'CustomMenu', component: CustomMenuComponent },
          { path: 'billing', component: BillingComponent },
          { path: 'students', component: StudentsComponent },
          { path: 'profile', component: ProfileComponent },
          { path: 'changePassword', component: ChangePasswordComponent },
          { path: 'storage', component: StorageComponent },
        ]
      }
    ]
    )
  ],
  declarations: [
    AccountComponent,

    UserComponent,
    AdvisorInformationComponent,
    AdvisorConfigurationComponent,
    OrganizationConfigurationComponent,
    OrganizationInformationComponent,
    SurveyComponent,
    TaggedListComponent,
    ResourceComponent,
    CollegeConfigurationComponent,
    StudentCategoriesComponent,
    CustomMenuComponent,
    BillingComponent,
    StudentsComponent,
    ProfileComponent,
    FamilyComponent,
    StudentComponent,
    ChangePasswordComponent,
    SurveyCreateComponent,
    SurveySectionCreateComponent,
    SurveyQuestionCreateComponent,
    SurveySectionItemComponent,
    SurveyQuestionItemComponent,
    SurveyChoiceItemComponent,
    SurveySectionTemplateComponent,
    EditApplicationComponent,
    EditMenuComponent,
    CreateUserComponent,
    TaggedListEditComponent,
    ManageUserComponent,
    StorageComponent,
    EditCategoryComponent,
    MenuComponent,
  ],
  exports: [
    EditApplicationComponent,
    EditMenuComponent,
    CreateUserComponent,
    ManageUserComponent,
    SurveySectionTemplateComponent,
    EditCategoryComponent,
  ],
  entryComponents: [
    EditApplicationComponent,
    EditMenuComponent,
    CreateUserComponent,
    SurveySectionTemplateComponent,
    ManageUserComponent,
    EditCategoryComponent,
  ],
})
export class AccountModule { }
