import { Component, OnInit, ViewChild, EventEmitter, Input, Output } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { SurveyCreateService } from '../../../service/survey-create.service';
import { ActivatedRoute } from '@angular/router';
import { MatHorizontalStepper } from '@angular/material/stepper';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
import { SurveySectionTemplateComponent } from '../survey-section-template/survey-section-template.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';


@Component({
  selector: 'app-survey-section-create',
  templateUrl: './survey-section-create.component.html',
  styleUrls: ['./survey-section-create.component.css']
})
export class SurveySectionCreateComponent implements OnInit {
  @Input() group: FormGroup;
  @Output() groupChange: EventEmitter<FormGroup> = new EventEmitter<FormGroup>();
  section:any={
    questions : []
  };
  sectionID=0;
  types:any={};
  constructor(private _formBuilder: FormBuilder, private scs:SurveyCreateService, private dialog:MatDialog) { }

  bindValue(section){
    this.section=section;
    this.group.get('nameCtrl').setValue(section.Name);
    this.group.get('descCtrl').setValue(section.Instructions);
    this.group.get('numCtrl').setValue(section.DisplayQuestionNumber==1);
  }
  ngOnInit() {
    
    this.initControl();
    this.scs.sectionEvent.subscribe(action=>{
      console.log(action);
      this.bindValue(this.scs.getSection());
    });
    this.scs.initSection();
    this.getQuestionType();
    this.scs.configEvent.subscribe(res=>{
      this.getQuestionType();
    })
  }
  getQuestionType(){
    this.types=this.scs.getQuestionTypesMap();
  }
  initControl(){
    this.group = this._formBuilder.group({
      nameCtrl: ['', Validators.required],
      descCtrl: ['',],
      numCtrl:[false,]
    });
    this.groupChange.emit(this.group);
  }
  save(){
    if(this.group.invalid){
      this.group.updateValueAndValidity();
      return;
    }
    this.scs.updateSection(this.prepareSection());
    this.scs.previous();
  }
  createQuestion(){
    if(this.group.invalid){
      this.group.updateValueAndValidity();
      return;
    }
    else {
      if(!this.scs.checkCreatedSection()){
        this.scs.updateSection(this.prepareSection());
      }
      this.scs.next();
      this.scs.initQuestion();
    }
  }
  cancel(){
    this.scs.initSection();
    this.scs.previous();
  }
  prepareSection(){
    let section= {
      Name: this.group.get('nameCtrl').value,
      Instructions: this.group.get('descCtrl').value,
      DisplayQuestionNumber: this.group.get('numCtrl').value?1:0
    }
    return section;
  }

  drop(event: CdkDragDrop<string[]>) {
    this.scs.moveQuestion(event.previousIndex, event.currentIndex);
    moveItemInArray(this.section.questions, event.previousIndex, event.currentIndex);
  }
  move(event){
    moveItemInArray(this.section.questions, event.start, event.end);
  }
  createQuestionWithTemplate(){
    if(this.group.invalid){
      this.group.updateValueAndValidity();
      return;
    }
    else {
      if(!this.scs.checkCreatedSection()){
        this.scs.updateSection(this.prepareSection()).then(res=>this.openTemplate());
      }
      else this.openTemplate();
    }
    
  }
  openTemplate(){
    const dialogRef = this.dialog.open(SurveySectionTemplateComponent, {
      width: '250px',
      data: {
        scs:this.scs
      }
    });
    this.scs.createQuestionWithTemplate(dialogRef);
  }
}
