import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';

@Component({
  selector: 'app-advisor-configuration',
  templateUrl: './advisor-configuration.component.html',
  styleUrls: ['./advisor-configuration.component.css']
})
export class AdvisorConfigurationComponent implements OnInit {

  public advisorConfiguration;
  public saving;
  public saved;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService) { 
    this.saving=false;
    this.saved=false;
    this.get_advisor_configuration();
  }

  ngOnInit() {
    
  }

  get_advisor_configuration(){
    this.accountService.api_advisor_configuration().then(res => {
      this.advisorConfiguration=res;
    });
  }
  
  //
  save_configuration(){
    this.saving=true;
    this.accountService.api_save_advisor_configuration(this.advisorConfiguration).then(res => {
      this.saving=false;
      this.get_advisor_configuration();
      
      this.saved=true;
      setTimeout(()=>{    
        this.saved=false;
      }, 3000);
    });
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}
