import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';
import { EditMenuComponent } from './edit-menu/edit-menu.component';

@Component({
  selector: 'app-custom-menu',
  templateUrl: './custom-menu.component.html',
  styleUrls: ['./custom-menu.component.css']
})
export class CustomMenuComponent implements OnInit {

  public saving;
  public saved;
  
  public customeMenu;
  public menuID;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService, public dialog: MatDialog) { 
    this.saving=false;
    this.saved=false;
    this.get_customeMenu();
  }

  ngOnInit() {
    
  }

  get_customeMenu(){
    this.accountService.api_customeMenu().then(res => {
      this.customeMenu=res;
    });
  }
  
  save_menu(){
    this.saving=true;
    this.accountService.api_save_custom_menu(this.customeMenu).then(res => {
      this.saving=false;
      this.get_customeMenu();
      
      this.saved=true;
      setTimeout(()=>{    
        this.saved=false;
      }, 3000);
      
      this.auth.refresh_userinfo();
    });
  }
  
  select_menu(menuID){
    if(this.menuID==menuID){
      this.menuID=0;
    }
    else{
      this.menuID=menuID;
    }
  }
  
  add_menu(){
    this.openDialog('add');
  }
  
  update_menu(){
    if(!this.menuID){
      alert("Please select a Menu Item from the table and try again");
    }else{
      this.openDialog('update');
    }
  }
  
  delete_menu(){
    if(!this.menuID){
      alert("Please select a Menu Item from the table and try again");
    }else{
      let menu={};
      for(let v of this.customeMenu.organization_items){
        if(v.ID==this.menuID){
          menu=v;
        }
      }
      
      this.accountService.api_delete_menu({'menu':menu}).then(res => {
        this.get_customeMenu();
        this.auth.refresh_userinfo();
      });
    }
  }
  
  //
  openDialog(action){
    let menu={};
    
    if(action!='add'){
      for(let v of this.customeMenu.organization_items){
        if(v.ID==this.menuID){
          menu=v;
        }
      }
    }
    
    const dialogRef = this.dialog.open(EditMenuComponent, {
      width: '550px',
      data: {"menu":menu,'action':action}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result){
        this.get_customeMenu();
        this.auth.refresh_userinfo();
      }
    });
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}
