import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';

@Component({
  selector: 'app-organization-information',
  templateUrl: './organization-information.component.html',
  styleUrls: ['./organization-information.component.css']
})
export class OrganizationInformationComponent implements OnInit {

  public organizationProfile;
  public saving;
  public saved;
  public photoNote;
  public photo;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService){ 
    this.saving=false;
    this.saved=false;
    this.get_organization_profile();
    this.photoNote=false;
    this.photo=this.auth.userinfo.login_user.federation_avatar;
  }

  ngOnInit() {
    
  }
  
  get_organization_profile(){
    this.accountService.api_organization_profile().then(res => {
      this.organizationProfile=res;
    });
  }
  
  //
  save_profile(){
    this.saving=true;
    this.accountService.api_save_organization_profile(this.organizationProfile).then(res => {
      this.saving=false;
      this.get_organization_profile();
      
      this.saved=true;
      setTimeout(()=>{    
        this.saved=false;
      }, 3000);
      
      //session
      if(this.photoNote){
        this.auth.refresh_userinfo();
        this.photoNote=false;
      }
    });
  }
  
  //
  uploadFile(event) {
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]); 
      reader.onload = (event) => {
        this.photo = reader.result;
        this.photoNote=true;
        this.organizationProfile['federation_photo']=reader.result;
      }
    }
  }
  
  //
  edit_advisor(advisor_info){
    let data={'advisor_info':advisor_info};
    this.accountService.api_save_organization_advisor_auth(data).then(res => {
      
    });
  }

}





















