import { Component, OnInit, Inject} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { SurveyCreateService } from '../../../service/survey-create.service';
import { FormControl,Validators } from '@angular/forms';

@Component({
  selector: 'app-survey-section-template',
  templateUrl: './survey-section-template.component.html',
  styleUrls: ['./survey-section-template.component.css']
})
export class SurveySectionTemplateComponent implements OnInit {
  scs:SurveyCreateService;
  templates = [];
  nameCtrl:FormControl;
  numCtrl:FormControl;
  requiredCtrl:FormControl;
  constructor(public dialogRef: MatDialogRef<SurveySectionTemplateComponent>,
    @Inject(MAT_DIALOG_DATA) public data) { 
      this.scs=data.scs;
      this.templates=Object.values(this.scs.getQuestionTemplates());
      console.log(this.templates);
      this.initControls();
    }

  ngOnInit() {
  }

  initControls(){
    this.nameCtrl= new FormControl(null,[Validators.required]);
    this.numCtrl= new FormControl(1);
    this.requiredCtrl= new FormControl(0);
  }

  prepareTemplate(){
    let template={
      templateID:this.nameCtrl.value,
      required:this.requiredCtrl.value,
      num_question:this.numCtrl.value
    }
    return template;
  }
  save(){
    this.dialogRef.close(this.prepareTemplate());
  }
  cancel(){
    this.dialogRef.close();
  }
}
