import { Component, OnInit, ViewChild, Input, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AccountService } from '../../../service/account.service';

@Component({
  selector: 'app-edit-category',
  templateUrl: './edit-category.component.html',
  styleUrls: ['./edit-category.component.css']
})
export class EditCategoryComponent implements OnInit {

  public ID;
  public user_info;
  public student_group;
  public post_data;
  public action;
  
  public student_info;
  
  constructor(public router:Router, public accountService: AccountService, public dialogRef: MatDialogRef<EditCategoryComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { 
    this.ID=data.ID;
    this.user_info=data.user_info;
    this.student_group=data.student_group;
    this.post_data={};
    this.action=data.action;
    
    if(this.action=='update'){
      this.get_student_group();
    }
  }

  ngOnInit() {
    
  }
  
  cancel(){
    this.dialogRef.close();
  }
  
  check_form(){
    if(!this.post_data.Name){
      alert("Please specify a name for this student category and try again.");  
    }else if(!this.post_data.Description){
      alert("Please specify a description for this student category and try again.");  
    }else if(!this.post_data.advisor){
      alert("Please specify a advisor for this student category and try again.");  
    }else{
      return true;  
    }
    
    return false;
  }
  
  save(){
    if(this.check_form()){
      if(this.action=='create'){
        this.accountService.api_create_student_group({'data':this.post_data}).then(res => {
          this.dialogRef.close(true);
        });
      }
      
      if(this.action=='update'){
        this.accountService.api_update_student_group({'data':this.post_data}).then(res => {
          this.dialogRef.close(true);
        });
      }
    }
  }

  get_student_group(){
    this.accountService.api_get_student_group(this.ID).then(res => {
      this.post_data={
        "ID":this.ID,
        "Name":res.StudentGroups.Name,
        "Description":res.StudentGroups.Description,
        "advisor":res.advisor,
      };
      this.student_info=res.StudentGroupStudents;
    });
  }


}
























