import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AuthService } from '../../../service/auth.service';
import { SurveyCreateService } from '../../../service/survey-create.service';

@Component({
  selector: 'app-survey-question-item',
  templateUrl: './survey-question-item.component.html',
  styleUrls: ['./survey-question-item.component.css']
})
export class SurveyQuestionItemComponent implements OnInit {
  public detail= false;
  @Input() question : any;
  @Input() index : any;
  @Output() orderChange:EventEmitter<any>= new EventEmitter<any>();
  types:any={};
  constructor(private auth:AuthService, private scs: SurveyCreateService) {


   }

  ngOnInit() {
    this.getQuestionType();
    this.scs.configEvent.subscribe(res=>{
      this.getQuestionType();
    })
  }
  getQuestionType(){
    this.types=this.scs.getQuestionTypesMap();
  }
  edit(ID){
    this.scs.next();
    this.scs.initQuestion(ID);
  }

  move(direction:number){
    this.scs.moveQuestion(this.index, this.index+direction);
    this.orderChange.emit({start:this.index, end:this.index+direction});
  }
  delete(){
    this.scs.deleteQuestion(this.question.ID);

  }
  copy(){
    this.scs.copyQuestion(this.question.ID);
  }
}
