import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';
import { CreateUserComponent } from './create-user/create-user.component';
import { ManageUserComponent } from './manage-user/manage-user.component';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  public saving;
  public saved;
  public msg;

  public base_info;
  public keyword;
  public search_result;
  public userPool;
  public select_count;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService, public dialog: MatDialog) {
    this.get_base_info();
    this.select_count=0;
    this.userPool=[];
  
    this.saving=false;
    this.saved=false;
    this.msg="";
  }

  ngOnInit() {
    
  }
  
  //
  get_base_info(){
    this.accountService.api_get_base_info().then(res => {
      this.base_info=res;
    });
  }
  
  //
  open_create_user(user_type){
    const dialogRef = this.dialog.open(CreateUserComponent, {
      width: '550px',
      height: '800px',
      data: {'user_info':this.auth.userinfo,'base_info':this.base_info,'user_type':user_type}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result!==undefined && result.studentID!=0){
        this.select_student(result.studentID);
      }
    });
  }
  
  //
  search_user(){
    this.search_result=[];
    if(this.keyword!=""){
      let tmp=[];
      for(let v of this.base_info.all_users){
        if((v['Firstname']!=null) && v['Firstname'].toLowerCase().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }
        
        if((v['Lastname']!=null) && v['Lastname'].toLowerCase().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }
        
        if((v['UserName']!=null) && v['UserName'].toLowerCase().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }
          
        if(v['ID'].toString().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }  
      }  
    }
  }
  
  //
  select_user(value){
   let check=false;
    
    if(this.userPool.length>0){
      for(let k in this.userPool){
        if(this.userPool[k].ID==value.ID){
          check=true;
          break;
        }
      }  
    }
    
    if(!check){
      this.userPool.push(value);
    }
      
    this.search_result=[];
    this.keyword="";
  }
  
  unselect_user(ID){
    for(let k in this.userPool){
      if(this.userPool[k].ID==ID){
        this.userPool.splice(k, 1);
      }
    }
  }
  
  //
  select_student(studentID){
    this.auth.api_select_student(studentID).then(res => {
      this.auth.get_userinfo();
      this.auth.getUserinfo_flag.subscribe(res => {
        this.router.navigateByUrl("/dashboard/index");
      });
    });
  }
  
  //
  send_user_registration(){
    this.accountService.api_send_user_registration({'data':this.userPool}).then(res => {
      this.show_msg(res);
    });
  }
  
  change_user_name(){
    this.open_manage_user("change_user_name");
  }
  
  change_user_email(){
    this.open_manage_user("change_user_email");
  }
  
  send_user_password(){
    this.accountService.api_send_user_password({'data':this.userPool}).then(res => {
      this.show_msg(res);
    });
  }
  
  user_prospectToActive(){
    this.open_manage_user("prospectToActive");
  }
  
  user_EFCToActive(){
    this.accountService.api_user_EFCToActive({'data':this.userPool}).then(res => {
      this.show_msg(res);
    });
  }
  
  user_archive(){
    if(confirm("Are you sure you want to archive this account?")) {
      this.accountService.api_user_archive({'data':this.userPool}).then(res => {
        this.show_msg(res);
      });
    }
  }
  
  user_delete(){
    if(confirm("Are you sure you want to prevent this user from using GuidedPath?")) {
      this.accountService.api_user_delete({'data':this.userPool}).then(res => {
        this.show_msg(res);
        
        for(let k in this.userPool){
          for(let kk in this.base_info.all_users){
            if(this.base_info.all_users[kk].ID==this.userPool[k].ID){
              this.base_info.all_users.splice(kk, 1);
            }
          }
          
          for(let kk in this.base_info.connection){
            if(this.base_info.connection[kk].ID==this.userPool[k].ID){
              this.base_info.connection.splice(kk, 1);
            }
          }
          
          for(let kk in this.base_info.student){
            if(this.base_info.student[kk].ID==this.userPool[k].ID){
              this.base_info.all_users.splice(kk, 1);
            }
          }
        }
        
        this.userPool=[];
      });
    }
    
  }
  
  user_lock(){
    if(confirm("Are you sure you want to prevent this user from using GuidedPath?")) {
      this.accountService.api_user_lock({'data':this.userPool}).then(res => {
        this.show_msg(res);
      });
    }
  }
  
  user_unlock(){
    this.accountService.api_user_unlock({'data':this.userPool}).then(res => {
      this.show_msg(res);
    });
  }
  
  change_user_advisor(){
    this.open_manage_user('change_advisor');
  }
  
  //
  open_manage_user(action){
    const dialogRef = this.dialog.open(ManageUserComponent, {
      width: '550px',
      height: '500px',
      data: {'manage_info':this.userPool,'user_info':this.auth.userinfo,'base_info':this.base_info,'action':action}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result!==undefined){
        this.show_msg(result);
      }
    });
  }
  
  show_msg(result){
    this.saved=true;
    this.msg=result.msg;
    
    setTimeout(()=>{    
      this.msg="";
      this.saved=false;
    }, 3000);
  }
  
  
  
  
  
  
  

  
  
  
  
  
  
  
  
  
  
  
  
}
