import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AuthService } from '../../../service/auth.service';
import { SurveyCreateService } from '../../../service/survey-create.service';
@Component({
  selector: 'app-survey-choice-item',
  templateUrl: './survey-choice-item.component.html',
  styleUrls: ['./survey-choice-item.component.css']
})
export class SurveyChoiceItemComponent implements OnInit {
  public detail= false;
  @Input() choice : any;
  @Input() index : any;
  @Output() orderChange:EventEmitter<any>= new EventEmitter<any>();
  constructor(private auth:AuthService, private scs: SurveyCreateService) {


   }

  ngOnInit() {
  }

  // edit(ID){
  //   this.scs.next();
  //   this.scs.initChoice(ID);
  // }

  move(direction:number){
    console.log('a');
    this.scs.moveChoice(this.index, this.index+direction);
    this.orderChange.emit({start:this.index, end:this.index+direction});
  }
  delete(){
    // this.scs.deleteQuestion(this.question.ID);
    this.scs.deleteChoice(this.choice.ID);
  }
}
