import { Component, OnInit, ViewChild, Input} from '@angular/core';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../../service/auth.service';
import { AccountService } from '../../../service/account.service';
import { FactoryService } from '../../../service/factory.service';
import { CreateUserComponent } from '../../user/create-user/create-user.component';

@Component({
  selector: 'app-family',
  templateUrl: './family.component.html',
  styleUrls: ['./family.component.css']
})
export class FamilyComponent implements OnInit{

  public init;
  public family_list;
  public family_profile;
  public family_profileID;
  public menuSelect;
  public base_info;
  
  public saving;
  public saved;
  public msg;
  public photo;
  public photoNote;
  public keyword;
  public search_result;
  public userPool;
  @Input() connectionType:string;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService, public dialog: MatDialog, public route:ActivatedRoute) { 
    this.saving=false;
    this.saved=false;
    this.photoNote=false;
    this.get_base_info();
    this.userPool=[];
    this.menuSelect=1;
  }

  ngOnInit() {
    //url
    if(this.connectionType==undefined){
      this.route.queryParams.subscribe(res=>{
        if(res.connectionType!=undefined && res.family_profileID!=undefined){
          this.connectionType=res.connectionType;
          this.family_profileID=res.family_profileID;
        }
        
        this.get_family_list();
      })
    }else{
      this.get_family_list();
    }
  }
  
  //
  get_family_list() {
    this.accountService.api_family_list(this.connectionType).then(res => {
      if(res){
        this.family_list=res;
        if(!this.family_profileID && this.family_list[0]!==undefined){
          this.get_family_profile(this.family_list[0]['ID']);
        }else{
          //url
          for(let k in this.family_list){
            if(this.family_list[k]['ID']==this.family_profileID){
              this.menuSelect=parseInt(k)+1;
            }
          }
          this.get_family_profile(this.family_profileID);
        }
      }
      
      this.init=true;
    });
  }
  
  //
  get_family_profile(ID){
    this.accountService.api_family_profile(ID).then(res => {
      this.family_profile=res;
      this.photo=this.family_profile.general_information.avatar;
    });
  }
  
  //
  select_connection(event: MatTabChangeEvent){
    this.family_profile=false;
    if(event.index!=0){
      this.get_family_profile(this.family_list[event.index-1]['ID']);
    }
  }
  
  //
  save_profile(){
    this.saving=true;
    this.accountService.api_save_family_profile(this.family_profile).then(res => {
      this.saving=false;
      this.get_family_list();
      
      this.saved=true;
      setTimeout(()=>{    
        this.saved=false;
      }, 3000);
    });
  }
  
  //
  uploadFile(event) {
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]); 
      reader.onload = (event) => {
        this.photo=reader.result;
        this.photoNote=true;
        this.family_profile['user_photo']=reader.result;
      }
    }
  }
  
  //
  get_base_info(){
    this.accountService.api_get_connection_base_info().then(res => {
      this.base_info=res;
      console.log(this.base_info);
    });
  }
  
  //
  open_create_user(user_type){
    this.auth.userinfo.connection=true;
    const dialogRef = this.dialog.open(CreateUserComponent, {
      width: '550px',
      height: '800px',
      data: {'user_info':this.auth.userinfo,'base_info':this.base_info,'user_type':user_type}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result!==undefined){
        this.get_family_list();
      }
    });
  }
  
  
  //
  search_user(){
    this.search_result=[];
    if(this.keyword!=""){
      let tmp=[];
      for(let v of this.base_info.connection){
        if((v['Firstname']!=null) && v['Firstname'].toLowerCase().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }
        
        if((v['Lastname']!=null) && v['Lastname'].toLowerCase().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }
        
        
        if((v['UserName']!=null) && v['UserName'].toLowerCase().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }
          
        if(v['ID'].toString().includes(this.keyword.toLowerCase())){
          if(tmp.indexOf(v.ID)==-1){
            this.search_result.push(v);
            tmp.push(v.ID);
          }
        }  
      }  
    }
    
  }
  
  //
  select_user(value){
   let check=false;
    
    if(this.userPool.length>0){
      for(let k in this.userPool){
        if(this.userPool[k].ID==value.ID){
          check=true;
          break;
        }
      }  
    }
    
    if(!check){
      this.userPool.push(value);
    }
      
    this.search_result=[];
    this.keyword="";
  }
  
  //
  unselect_user(ID){
    for(let k in this.userPool){
      if(this.userPool[k].ID==ID){
        this.userPool.splice(k, 1);
      }
    }
  }
  
  //
  associate_connection(ID){
    this.accountService.api_associate_connection(ID).then(res => {
      if(!res.status){
        alert(res.msg);
      }
      else{
        this.show_msg(res);
        this.userPool=[];
        this.search_result=[];
        this.keyword="";
        this.get_family_list();
      }
    });
  }
  
  //
  delete_connection(){
    console.log(this.family_profile);
    this.accountService.api_delete_connection(this.family_profile.account_information.userID).then(res => {
      this.get_family_list();
    });
  } 
  
  show_msg(result){
    this.saved=true;
    this.msg=result.msg;
    
    setTimeout(()=>{    
      this.msg="";
      this.saved=false;
    }, 3000);
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  

}
