import { Injectable, EventEmitter } from '@angular/core';
import { FactoryService } from './factory.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { MatHorizontalStepper } from '@angular/material/stepper';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';

@Injectable({
  providedIn: 'root'
})
export class SurveyCreateService extends FactoryService{
  SurveyID:number = 0;
  SurveySectionID:number = 0;
  SurveyQuestionID: number = 0;
  survey={
    sections:[]
  };
  section={
    questions:[]
  };
  question={
    SurveyQuestionTypeID:1,
    choices:[]
  };
  surveys:any=[];

  listEvent:EventEmitter<any>=new EventEmitter<any>();
  surveyEvent:EventEmitter<any>=new EventEmitter<any>();
  sectionEvent:EventEmitter<any>=new EventEmitter<any>();
  questionEvent:EventEmitter<any>=new EventEmitter<any>();
  configEvent:EventEmitter<any>=new EventEmitter<any>();

  stepper:MatHorizontalStepper;
  config:any={};
  constructor(protected http: HttpClient) {
    super(http);
    this.getSurveyConfigs();
  }
  setStepper(stepper:MatHorizontalStepper){
    this.stepper=stepper;
  }
  previous(){
    this.stepper.previous();
  }
  next(){
    this.stepper.next();
  }
  checkCreated():boolean{
    return this.SurveyID!=0;
  }
  checkCreatedSection():boolean{
    return this.SurveySectionID!=0;
  }
  checkCreatedQuestion():boolean{
    return this.SurveyQuestionID!=0;
  }
  createNewSurvey(survey:any):Promise<any>{
    let posts={
      survey:survey
    };
    return new Promise<any>(resolve=>{
      this.post('/survey/account/createNewSurvey/', posts).then(res=>{
        this.listEvent.emit(res);
        resolve(res);

      })
    });
  }
  createNewSection(section:any):Promise<any>{
    let posts={
      section:section,
      surveyID:this.SurveyID
    };
    return new Promise<any>(resolve=>{
      this.post('/survey/account/createNewSection/',posts).then(res=>{
        resolve(res);
      })
    });
  }
  createNewQuestion(question):Promise<any>{
    let posts={
      question:question,
      sectionID:this.SurveySectionID
    }
    return new Promise<any>(resolve=>{
      this.post('/survey/account/createNewQuestion/', posts).then(res=>{
        resolve(res);
      })
    });
  }
  createNewChoice(choice):Promise<any>{
    let posts={
      choice:choice,
      questionID:this.SurveyQuestionID
    }
    return new Promise<any>(resolve=>{
      this.post('/survey/account/createNewChoice/', posts).then(res=>{
        resolve(res);
      })
    });
  }
  getSurveyAndSections(surveyID):Promise<any>{
    return new Promise<any>(resolve=>{
      this.get('/survey/account/getSurveyAndSections/?surveyID='+surveyID).then(res=>{
        this.survey=res;
        this.surveyEvent.emit(res);
        resolve(res);
      });
    });
  }
  getSectionAndQuestions(sectionID):Promise<any>{
    return new Promise<any>(resolve=>{
      this.get('/survey/account/getSectionAndQuestions/?sectionID='+sectionID).then(res=>{
        this.section=res;
        this.sectionEvent.emit(res);
        resolve(res);
      });
    });
  }
  getQuestionAndChoices(questionID):Promise<any>{
    return new Promise<any>(resolve=>{
      this.get('/survey/account/getQuestionAndChoices/?questionID='+questionID).then(res=>{
        this.question=res;
        this.questionEvent.emit(res);
        resolve(res);
      });
    });
  }


  updateSurvey(survey){
    if(this.SurveyID==0){
      this.createSurvey(survey);
    }
    else {
      this.changeSurvey(survey).then(res=>{
        this.survey=res;
        this.surveyEvent.emit(res);
      });
    }
  }
  changeSurvey(survey){
    let posts={
      surveyID:this.SurveyID,
      survey:survey
    };
    return new Promise<any>(resolve=>{
      this.post('/survey/account/updateSurvey/', posts).then(res=>{
        resolve(res);
      })
    });
  }
  updateSection(section){
    return new Promise<any>(resolve=>{
      if(this.SurveySectionID==0){
        this.createSection(section).then(res=>{
          resolve(res);
        });
      }else {
        this.changeSection(section).then(res=>{
          this.section=res;
          this.getSurveyAndSections(this.SurveyID);
          this.sectionEvent.emit(res);
          resolve(res);
        });
      }
    })
  }
  changeSection(section){
    let posts={
      sectionID:this.SurveySectionID,
      section:section
    };
    return new Promise<any>(resolve=>{
      this.post('/survey/account/updateSection/', posts).then(res=>{
        resolve(res);
      })
    });
  }
  updateQuestion(question){
    if(this.SurveyQuestionID==0){
      this.createQuestion(question);
    }
    else {
      this.changeQuestion(question).then(res=>{
        this.question=res;
        this.getSectionAndQuestions(this.SurveySectionID);
        this.sectionEvent.emit(res);
      });
    }
  }
  changeQuestion(question){
    let posts={
      questionID:this.SurveyQuestionID,
      question:question
    };
    return new Promise<any>(resolve=>{
      this.post('/survey/account/updateQuestion/', posts).then(res=>{
        resolve(res);
      })
    });
  }


  createSurvey(survey){
    this.createNewSurvey(survey).then(res=>{
      this.SurveyID=res;
      this.getSurveyAndSections(res);
    })
  }

  createSection(section){
    return new Promise<any>(resolve=>{
      this.createNewSection(section).then(res=>{
        this.SurveySectionID=res;
        this.getSectionAndQuestions(res);
        this.getSurveyAndSections(this.SurveyID);
        resolve(res);
      })
    })
    
  }
  createQuestion(question){
    this.createNewQuestion(question).then(res=>{
      this.SurveyQuestionID=res;
      this.getQuestionAndChoices(res);
      this.getSectionAndQuestions(this.SurveySectionID);
    })
  }
  createChoice(choice){
    this.createNewChoice(choice).then(res=>{
      this.getQuestionAndChoices(this.SurveyQuestionID);
    })
  }
  moveSection(start:number, end :number){
    if(end<0||end>=this.survey.sections.length) return;
    this.movingSection(start, end);
  }
  movingSection(start:number, end:number){
    let section=this.survey.sections[start];
    let posts={
      start:start+1,
      end:end+1,
      sectionID:section.ID
    }
    return new Promise<any>(resolve=>{
      this.post('/survey/account/moveSection/', posts).then(res=>{
        this.survey=res;
        this.surveyEvent.emit(this.survey);
      })
    })
  }
  deleteSection(sectionID){
    this.post('/survey/account/deleteSection/', {sectionID:sectionID}).then(res=>{
      this.survey=res;
      this.surveyEvent.emit(this.survey);
    })
  }
  moveQuestion(start:number, end :number){
    if(end<0||end>=this.section.questions.length) return;
    this.movingQuestion(start, end);
  }
  movingQuestion(start:number, end:number){
    let question=this.section.questions[start];
    let posts={
      start:start+1,
      end:end+1,
      questionID:question.ID
    }
    return new Promise<any>(resolve=>{
      this.post('/survey/account/moveQuestion/', posts).then(res=>{
        this.section=res;
        this.sectionEvent.emit(this.section);
      })
    })
  }
  moveChoice(start:number, end:number){
    if(end<0||end>=this.question.choices.length) return;
    this.movingChoice(start, end);
  }
  movingChoice(start:number, end:number){
    let choice=this.question.choices[start];
    let posts={
      start:start+1,
      end:end+1,
      choiceID:choice.ID
    }
    return new Promise<any>(resolve=>{
      this.post('/survey/account/moveChoice/', posts).then(res=>{
        this.question=res;
        this.questionEvent.emit(this.question);
      })
    })
  }

  deleteChoice(choiceID){
    this.post('/survey/account/deleteChoice/', {choiceID:choiceID}).then(res=>{
      this.question=res;
      this.questionEvent.emit(this.question);
    })
  }

  deleteQuestion(questionID){
    this.post('/survey/account/deleteQuestion/', {questionID:questionID}).then(res=>{
      this.section=res;
      this.sectionEvent.emit(this.section);
    })
  }
  deleteSurvey(surveyID){
    this.post('/survey/account/deleteSurvey/', {surveyID:surveyID}).then(res=>{
      this.surveys=res;
      this.listEvent.emit(res);
    })
  }

  initSurvey(surveyID=0){
    this.resetSurvey();
    if(surveyID==0){
      // this.createSurvey();
    }
    else {
      this.SurveyID=surveyID;
      this.getSurveyAndSections(surveyID);
    }
    
  }
  initSection(sectionID=0){
    this.resetSection();
    if(sectionID==0){
      
    }
    else {
      this.SurveySectionID=sectionID;
      this.getSectionAndQuestions(sectionID);
    }
  }
  initQuestion(questionID=0){
    this.resetQuestion();
    if(questionID==0){

    }
    else {
      this.SurveyQuestionID=questionID;
      this.getQuestionAndChoices(questionID);
    }
  }


  resetSurvey(){
    this.SurveyID=0;
    this.survey={
      sections:[]
    };
    this.surveyEvent.emit(this.survey);
    this.resetSection();
  }
  resetSection(){
    this.SurveySectionID=0;
    this.section={
      questions:[]
    };
    this.sectionEvent.emit(this.section);
    this.resetQuestion();
  }
  resetQuestion(){
    this.question={
      SurveyQuestionTypeID:1,
      choices:[]
    };
    this.SurveyQuestionID=0;

    this.questionEvent.emit(this.question);
  }

  private getSurveyConfigs(){
    this.get('/survey/account/getSurveyConfigs/').then(res=>{
      this.config=res;
      this.configEvent.emit(this.config);
    })
  }
  getQuestionTemplates(){
    return this.config.question_templates;
  }
  getQuestionTypes(){
    console.log(this.config.question_types);

    let res=Object.values(this.config.question_types);
    return res;
  }
  getQuestionTypesMap(){

    return this.config.question_types;
  }
  getChoiceDisplayTypes(){
    return this.config.choice_display_types;
  }
  getTextDisplayTypes(){
    return this.config.text_display_types;
  }
  getQuestionTypeConfigs(){
    return this.config.type_config;
  }
  getSurvey(){
    return this.survey;
  }
  getSection(){
    return this.section;
  }
  getQuestion(){
    return this.question;
  }
  createQuestionWithTemplate(dialogRef){
    
    dialogRef.afterClosed().subscribe(result=>{
      
      if(result.templateID!== undefined){
        if(this.SurveySectionID==0){
          return;
        }
        result['sectionID']=this.SurveySectionID;
        this.post('/survey/account/createNewQuestionWithTemplate/', result).then(res=>{
          this.section=res;
          this.sectionEvent.emit(res);
        })
      }
    })
  }
  getSurveyList(){
    return new Promise<any>(resolve=>{
      this.get('/survey/account/getSurveyList/').then(res=>{
        this.surveys=res;
        this.listEvent.emit(res);
      })
    })
  }
  getSurveys(){
    return this.surveys;
  }
  setAvailable(surveyID, status){
    let posts={
      surveyID:surveyID,
      status:status
    }
    this.post('/survey/account/setAvailable/',posts).then(res=>{
      if(res!=0){
        this.surveys=res;
        this.listEvent.emit(res);
      }
    })
  }
  copyQuestion(questionID){
    let posts={
      questionID:questionID,
      sectionID:this.SurveySectionID
    }
    this.post('/survey/account/copyQuestion/', posts).then(res=>{
      this.section=res;
      this.sectionEvent.emit(res);
    })
  }
  copySection(sectionID){
    let posts={
      sectionID:sectionID,
      surveyID:this.SurveyID
    }
    this.post('/survey/account/copySection/', posts).then(res=>{
      this.survey=res;
      this.surveyEvent.emit(res);
    })
  }
  copySurvey(surveyID){
    let posts={
      surveyID:surveyID
    }
    this.post('/survey/account/copySurvey/', posts).then(res=>{
      this.surveys=res;
      this.listEvent.emit(res);
    })
  }
}
