import { Component, OnInit, ViewChild } from '@angular/core';
import { SurveyCreateService } from '../../service/survey-create.service';
import {MatSort, MatTableDataSource} from '@angular/material';
import { Router } from '@angular/router';

@Component({
  selector: 'app-survey',
  templateUrl: './survey.component.html',
  styleUrls: ['./survey.component.css'],
  providers:[SurveyCreateService]
})
export class SurveyComponent implements OnInit {
  surveys:any={
    owned:[],
    succeeded:[]
  };
  owned : MatTableDataSource<SurveyElement>;
  succeeded : MatTableDataSource<SurveyElement>;
  owned_column:string[]=['Name','Description','Completed', 'num_section','LastModified', 'Edit'];
  succeeded_column:string[]=['Name','Description', 'Available'];

  @ViewChild('matSort1') sort1: MatSort;
  @ViewChild('matSort2') sort2: MatSort;
  constructor(private scs: SurveyCreateService, private router:Router) { 
  }

  ngOnInit() {  
    this.scs.listEvent.subscribe(res=>{
      this.surveys=this.scs.getSurveys();
      this.owned=new MatTableDataSource<SurveyElement>(this.surveys.owned);
      this.succeeded=new MatTableDataSource<SurveyElement>(this.surveys.succeeded);

    this.owned.sort = this.sort1;
    this.succeeded.sort=this.sort2;
    });
    this.scs.getSurveyList();
  }
  setAvailable(element,$event){
    console.log($event);
    let visibleID=element.visibleID==undefined?0:element.visibleID;
    this.scs.setAvailable(element.ID, $event.checked?1:0);
  }
  edit(ID){
    this.router.navigateByUrl('/account/survey/edit/'+ID);
  }
  copy(ID){
    this.scs.copySurvey(ID);
  }
  delete(ID){
    this.scs.deleteSurvey(ID);
  }
  createSurvey(){
    this.router.navigateByUrl('/account/survey/create');
  }
}
export interface SurveyElement {
  Name: string;
  OwnerFederationID: number;
  Description: string;
  LongDescription: string;
  num_section: number;
  IsActive:number;
  IsAvailable:number;
  LastModified:string;
  Completed:number;
}
