import { Component, OnInit , ViewChild, ChangeDetectorRef } from '@angular/core';
import { AccountService } from '../../../service/account.service';
import { Router, ActivatedRoute } from '@angular/router';
import {MatSort, MatTableDataSource} from '@angular/material';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {Observable, Observer} from 'rxjs';
import {SelectionModel} from '@angular/cdk/collections'
import {AuthService } from '../../../service/auth.service';

@Component({
  selector: 'app-tagged-list-edit',
  templateUrl: './tagged-list-edit.component.html',
  styleUrls: ['./tagged-list-edit.component.css']
})
export class TaggedListEditComponent implements OnInit {
  group: FormGroup;
  taggedPrograms: MatTableDataSource<TaggedProgramElement>;
  list_column:string[]=['select','Name','Location'];
  @ViewChild('matSort1') sort1: MatSort;
  pageinfo:any={};
  taggedlist:any={};
  list_types:any=[];
  programCtrl:FormControl;
  programs:Observable<Object[]>;
  ID_set:any={};

  selection:SelectionModel<number>;
  states:any={};

  constructor(private acc:AccountService, private router:Router,private route:ActivatedRoute,private _formBuilder: FormBuilder, private cd:ChangeDetectorRef, private auth:AuthService) { 
    this.pageinfo = this.route.snapshot.data;
    this.initControl();
    this.acc.getTaggedListTypeMap().then(res=>{
      this.list_types=Object.values(res);
    })

    this.programCtrl= new FormControl(null,[this.programValidator]);
    const initialSelection = [];
const allowMultiSelect = true;
this.selection = new SelectionModel<number>(allowMultiSelect, initialSelection);
    this.auth.getUserinfo_flag.subscribe(res=>{
      this.getStateConfig();
    });
    this.getStateConfig();
  }

  getStateConfig(){
    const states=this.auth.getConfig('states');
    for(let state of states){
      this.states[state.ID]= state;
    }
  }

  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.taggedPrograms.data.length;
    return numSelected == numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.taggedPrograms.data.forEach(row => this.selection.select(row.ID));
  }


  ngOnInit() {
    if(this.pageinfo.type=='edit'){
      this.route.params.subscribe(data=>{
        this.acc.getTaggedList(data.id).then(res=>{
          this.asignData(res);
        })
      })
    }
    else {
      this.taggedPrograms=new MatTableDataSource<TaggedProgramElement>([]);
    }
    this.programs=new Observable<Object[]>(observer=>{
      this.programCtrl.valueChanges.subscribe(event=>{
        this.acc.getSchoolNameList(event).then(res=>{
          observer.next(this.filterPrograms(res));
        })
      })
    });
    
  }

  asignData(res){
    this.taggedlist=res;
          this.taggedPrograms= new MatTableDataSource<TaggedProgramElement>(res.programs);
          this.taggedPrograms.sort = this.sort1;
          this.initIDSet(res.programs);
          this.bindValue(res);
  }
  initIDSet(programs:Object[]){
    for(let program of programs){
      this.ID_set[program['ProgramID']]=true;
    }
  }
  filterPrograms(programs){
    let res=[];
    for(let program of programs){
      if(this.ID_set[program['id']]==true){
        continue;
      }
      else res.push(program);
    }
    return res;
  }
  initControl(){
    this.group = this._formBuilder.group({
      nameCtrl: ['', Validators.required],
      descCtrl: ['', Validators.maxLength(1000)],
      typeCtrl: [0,],
    });
  }
  bindValue(taggedlist){
    this.group.get('nameCtrl').setValue(taggedlist.Name);
    this.group.get('descCtrl').setValue(taggedlist.Description);
    this.group.get('typeCtrl').setValue(taggedlist.TaggedListTypeID);
  }
  prepareTaggedList(){
    let list={
      Name: this.group.get('nameCtrl').value,
      Description: this.group.get('descCtrl').value,
      TaggedListTypeID: this.group.get('typeCtrl').value,
    }
    return list;
  }
  save(){
    if(this.pageinfo.type=='create' && this.taggedlist.ID==undefined){
      this.acc.createNewTaggedList(this.prepareTaggedList()).then(res=>{
        if(res!==0) {
          this.taggedlist.ID=res;
          this.cancel();
        }
      });
    }
    else {
      this.acc.updateTaggedList(this.taggedlist.ID,this.prepareTaggedList()).then(res=>{
        if(res==1) this.cancel();
      });
    }

  }

  cancel(){
    this.router.navigateByUrl('/account/TaggedList');
  }
  addProgram(){
    if(this.programCtrl.invalid) return;
    let program=this.programCtrl.value;
    if(this.taggedlist.ID==undefined) {
      if(this.group.invalid) {
        this.group.updateValueAndValidity();
        return;
      }
      this.acc.createNewTaggedList(this.prepareTaggedList()).then(res=>{
        if(res!==0) this.taggedlist.ID=res;
        this.addNewProgram(program);
      });
    }
    else {
      this.addNewProgram(program);
    }
    
  }

  addNewProgram(program){
    this.acc.addProgramToTaggedList(program.id, this.taggedlist.ID).then(res=>{
      if(res!==0){
        this.ID_set[program.id]=true;
        this.taggedPrograms.data.push(res);
        this.taggedPrograms.data= [...this.taggedPrograms.data];
        this.cd.detectChanges();
      }
      this.programCtrl.setValue(null);
      this.programCtrl.reset();
    })
  }

  displayFn(program:any): string | undefined {
    return program ? program.name : undefined;
  }

  //check any program is chosen in the program name input autocomplete
  programValidator(control: FormControl){
    if(control.value==null||control.value==undefined||control.value.id==undefined){
      return {
        format:{
          id:'missing'
        }
      }
    }
    else return null;
  }
  
  delete(){
    console.log(this.selection.selected);
    let selectedlist=this.selection.selected;
    if(this.selection.hasValue()){
      this.acc.deleteProgramListFromTaggedList(selectedlist, this.taggedlist.ID).then(res=>{
        if(res!==0){
          let programlist=this.taggedPrograms.data;
          this.taggedPrograms.data=[...this.deleteSelected(programlist, selectedlist)];
          this.selection.clear();
        }
      });
    }
    
  }

  deleteSelected(programlist:Array<TaggedProgramElement>, selectedlist):any[]{
    let map=[];
    for(let id of selectedlist){
      map[id]=true;
    }
    programlist=programlist.filter(item=>{
      if(map[item['ID']]){
        this.ID_set[item['ProgramID']]=false;
        return false;
      }
      else {
        return true;
      }
    });
    return programlist;
  }
  print(){

  }
}
export interface TaggedProgramElement {
  ID:number;
  ProgramID:number;
  detail:Object
}