import { Component, OnInit, ViewChild, Input, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AccountService } from '../../../service/account.service';

@Component({
  selector: 'app-manage-user',
  templateUrl: './manage-user.component.html',
  styleUrls: ['./manage-user.component.css']
})
export class ManageUserComponent implements OnInit {
  
  public user_info;
  public base_info;
  public action;
  public post_data;
  public manage_info
  
  public result;
  
  constructor(public router:Router, public accountService: AccountService, public dialogRef: MatDialogRef<ManageUserComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {
    this.user_info=data.user_info;
    this.base_info=data.base_info;
    this.action=data.action;
    this.manage_info=data.manage_info;
    
    this.post_data={
      'email':'',
      'username':"",
      'advisor':this.user_info.login_user.id,
      'manage_info':this.manage_info,
      'sent_email':"0",
    };
  }

  ngOnInit() {
    
  }
  
  save(){
    
    if(this.action=='prospectToActive'){
      this.accountService.api_user_prospectToActive({'data':this.post_data}).then(res => {
        this.dialogRef.close(res);
      });
    }  
    
    if(this.action=='change_user_email'){
      this.accountService.api_change_user_email({'data':this.post_data}).then(res => {
          this.dialogRef.close(res);
      });
    }
    
    if(this.action=='change_user_name'){
      this.accountService.api_change_user_name({'data':this.post_data}).then(res => {
          this.dialogRef.close(res);
      });
    }
    
    
    if(this.action=='change_advisor'){
      this.accountService.api_change_user_advisor({'data':this.post_data}).then(res => {
          this.dialogRef.close(res);
      });
    }
    
    
  }
  
  cancel(){
    this.dialogRef.close();
  }

  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}
