import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AuthService } from '../../../service/auth.service';
import { SurveyCreateService } from '../../../service/survey-create.service';

@Component({
  selector: 'app-survey-section-item',
  templateUrl: './survey-section-item.component.html',
  styleUrls: ['./survey-section-item.component.css']
})
export class SurveySectionItemComponent implements OnInit {
  public detail= false;
  @Input() section : any;
  @Input() index : any;
  @Output() orderChange:EventEmitter<any>= new EventEmitter<any>();
  constructor(private auth:AuthService, private scs: SurveyCreateService) {


   }

  ngOnInit() {
  }
  edit(ID){
    this.scs.next();
    this.scs.initSection(ID);
  }
  move(direction:number){
    this.scs.moveSection(this.index, this.index+direction);
    this.orderChange.emit({start:this.index, end:this.index+direction});
  }
  delete(){
    this.scs.deleteSection(this.section.ID);
  }
  copy(){
    this.scs.copySection(this.section.ID);
  }
}
