import { Component, OnInit, ViewChild } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { SurveyCreateService } from '../../../service/survey-create.service';
import { ActivatedRoute, Router } from '@angular/router';
import { MatHorizontalStepper } from '@angular/material/stepper';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
import { FocusMonitor } from '@angular/cdk/a11y';

@Component({
  selector: 'app-survey-create',
  templateUrl: './survey-create.component.html',
  styleUrls: ['./survey-create.component.css'],
})
export class SurveyCreateComponent implements OnInit {

  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  thirdFormGroup: FormGroup;
  isLinear = true;
  survey:any={
    sections : []
  };
  pageinfo :any={};
  surveyID=0;
  @ViewChild('stepper') stepper:MatHorizontalStepper;
  constructor(private _formBuilder: FormBuilder, private scs: SurveyCreateService, private route:ActivatedRoute, private router:Router) { 
    this.pageinfo = this.route.snapshot.data;
    
  }

  bindValue(survey){
    this.survey=survey;
    this.firstFormGroup.get('nameCtrl').setValue(survey.Name);
    this.firstFormGroup.get('descCtrl').setValue(survey.Description);
    this.firstFormGroup.get('activeCtrl').setValue(survey.IsActive==1);
  }

  ngOnInit() {
    this.scs.setStepper(this.stepper);
    this.initControl();
    if(this.pageinfo.type=='edit'){
      this.getID();
    }
    this.scs.surveyEvent.subscribe(action=>{
      console.log(action);
      this.bindValue(this.scs.getSurvey());
    });
    console.log(this.surveyID);
    this.scs.initSurvey(this.surveyID);
  }

  getID(){
    this.route.params.subscribe(res=>{
      if(res['id']!==undefined){
        this.surveyID=res['id'];
      }
    })
  }


  initControl(){
    this.firstFormGroup = this._formBuilder.group({
      nameCtrl: ['', Validators.required],
      descCtrl: ['',],
      activeCtrl: [false,],
    });
  }

  check(){
    console.log(this.secondFormGroup);
    console.log(this.secondFormGroup.contains("firstCtrl"));
  }

  save(){
    if(this.firstFormGroup.invalid){
      this.firstFormGroup.updateValueAndValidity();
      return;
    }
    this.scs.updateSurvey(this.prepareSurvey());
    this.cancel();
  }
  cancel(){
    this.router.navigateByUrl('/account/survey');
  }

  prepareSurvey(){
    let survey= {
      Name: this.firstFormGroup.get('nameCtrl').value,
      Description: this.firstFormGroup.get('descCtrl').value,
      IsActive: this.firstFormGroup.get('activeCtrl').value?1:0
    }
    return survey;
  }

  createSection(){
    if(this.firstFormGroup.invalid){
      this.firstFormGroup.updateValueAndValidity();
      return;
    }
    else {
      if(!this.scs.checkCreated()){
        this.scs.updateSurvey(this.prepareSurvey());
      }
      this.stepper.next();
      this.scs.initSection();
    }
  }

  drop(event: CdkDragDrop<string[]>) {
    this.scs.moveSection(event.previousIndex, event.currentIndex);
    moveItemInArray(this.survey.sections, event.previousIndex, event.currentIndex);
  }

  move(event){
    moveItemInArray(this.survey.sections, event.start, event.end);
  }
}
