import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { AccountService } from '../../service/account.service';
import { Router } from '@angular/router';
import {MatSort, MatTableDataSource} from '@angular/material';
import {SelectionModel} from '@angular/cdk/collections'

@Component({
  selector: 'app-tagged-list',
  templateUrl: './tagged-list.component.html',
  styleUrls: ['./tagged-list.component.css']
})
export class TaggedListComponent implements OnInit {
  taggedLists: MatTableDataSource<TaggedListElement>;
  list_column:string[]=['select','Name','Description','TaggedListTypeID','num_program', 'LastModified','Action'];
  @ViewChild('matSort1') sort1: MatSort;
  list_types:any={};
  selection:SelectionModel<number>;
  constructor(private acc:AccountService, private router:Router, private cd:ChangeDetectorRef) { 
    this.acc.getTaggedListTypeMap().then(res=>{
      this.list_types=res;
    });
    const initialSelection = [];
const allowMultiSelect = true;
this.selection = new SelectionModel<number>(allowMultiSelect, initialSelection);
  }



  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.taggedLists.data.length;
    return numSelected == numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.taggedLists.data.forEach(row => this.selection.select(row.ID));
  }

  ngOnInit() {
    this.acc.getTaggedLists().then(res=>{
      this.taggedLists=new MatTableDataSource<TaggedListElement>(res);
      this.taggedLists.sort=this.sort1;
      console.log(res);
    });

  }
  edit(ID){
    this.router.navigateByUrl('/account/TaggedList/edit/'+ID);
  }

  deleteOne(ID){
    let list=[ID];
    this.delete(list);
  }

  delete(list=null){
    if(list==null){
      list=this.selection.selected;
    }
    this.acc.deleteTaggedLists(list).then(res=>{
      let remain=[];
      this.taggedLists.data=this.taggedLists.data.filter(item=>{
        if(res[item.ID])return false;
        else return true;
      });
      this.taggedLists.data=[...this.taggedLists.data];
      this.taggedLists.sort=this.sort1;
      this.cd.detectChanges();
      
      this.selection.clear();
    })
  }
  createNew(){
    this.router.navigateByUrl('/account/TaggedList/create');
  }
}
export interface TaggedListElement {
  ID:number;
  Name: string;
  OwnerFederationID: number;
  Description: string;
  num_program: number;
  TaggedListTypeID:number;
  LastModified:string;
 
}
