import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MatTabChangeEvent } from '@angular/material/tabs';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
    
  public menu;
  public menuSelect;
  public connectionType;

  constructor(public auth: AuthService, public router:Router, public route:ActivatedRoute) { 
    this.menu='Student';
    this.menuSelect=0;
  }

  ngOnInit() {
    this.route.queryParams.subscribe(res=>{
      if(res.menu!=undefined && res.menuSelect!=undefined){
        this.menu=res.menu;
        this.menuSelect=res.menuSelect;
      }
    })
  }
  
  onLinkClick(event: MatTabChangeEvent) {
    if(event.tab.textLabel=='Student'){
      this.menu='Student';
    }
    
    if(event.tab.textLabel=='Family'){
      this.menu='Family';
      this.connectionType='family';
    }
    
    if(event.tab.textLabel=='Other Connections'){
      this.menu='Other Connections';
      this.connectionType='connection';
    }
  }
}
