import { Component, OnInit, ViewChild, Input, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { AuthService } from '../../../service/auth.service';
import { AccountService } from '../../../service/account.service';
import { FactoryService } from '../../../service/factory.service';

@Component({
  selector: 'app-edit-application',
  templateUrl: './edit-application.component.html',
  styleUrls: ['./edit-application.component.css']
})
export class EditApplicationComponent implements OnInit {

  public application;
  public milestoneSelection;
  public action;
  
  constructor(public auth: AuthService, public accountService: AccountService, public dialogRef: MatDialogRef<EditApplicationComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { 
    
    this.application=data.application;
    this.milestoneSelection=data.milestoneSelection;
    this.action=data.action;
    
  }

  ngOnInit() {
    
  }
  
  cancel(){
    this.dialogRef.close(false);
  }
  
  save(){
    if(this.action=='update'){
      this.accountService.api_update_application({'application':this.application}).then(res => {
        this.dialogRef.close(true);
      });
    }
    
    if(this.action=='add'){
      if(!this.application.AdmissionMilestoneID){
        alert("You must specify an Admission Milestone from the list of milestones.");  
      }else{
        this.accountService.api_add_application({'application':this.application}).then(res => {
          this.dialogRef.close(true);
        });
      }
    }
    
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  

}
