import { Component, OnInit, ViewChild, Input, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { AccountService } from '../../../service/account.service';
import { AuthService } from '../../../service/auth.service';

@Component({
  selector: 'app-create-user',
  templateUrl: './create-user.component.html',
  styleUrls: ['./create-user.component.css']
})
export class CreateUserComponent implements OnInit {

  public user_info;
  public user_type;
  public base_info;
  public post_data;
  public user_type_list;
  
  constructor(public router:Router, public auth: AuthService, public accountService: AccountService, public dialogRef: MatDialogRef<CreateUserComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {
    
    this.user_info=data.user_info;
    this.base_info=data.base_info;
    this.select_user_type(data.user_type);
    
    this.post_data={
      'Email':'',
      'Firstname':'',
      'Lastname':'',
      'user_role':7,
      'organization':this.base_info.organization[0].ID,
      'registration':true,
      'application_year':2020,
      'advisor':this.user_info.login_user.id,
      'student_status':"IsActive",
      'student':this.base_info.student[0].ID,
      'connection_type':this.base_info.connection_type[0].ID,
      'user_type':this.user_type,
    };
  }
  
  select_user_type(user_type){
    this.user_type=user_type;
    this.user_type_list=this.base_info.user_role[user_type];
  }

  ngOnInit() {
  }

  cancel(){
    this.dialogRef.close();
  }
  
  check_form(){
    if(this.post_data.Email=='') {alert('Missing Email');return false;}
    if(this.post_data.Firstname=='') {alert('Missing Firstname');return false;}
    if(this.post_data.Lastname=='') {alert('Missing Lastname');return false;}
    
    return true;
  }
  
  save(go_profile){
    if(!this.check_form()) return;
    this.accountService.api_create_user({'post_data':this.post_data}).then(res => {
      
      if(this.post_data.registration){
        alert("A welcome email with registration credentials was sent to "+this.post_data.Email);
      }else{
        alert("This user will not receive a welcome email. Please provide the appropriate registration information to him/her as appropriate.");
      };
      
      if(go_profile){
        this.dialogRef.close({ 'studentID': res });
      }else{
        this.dialogRef.close({ 'studentID': 0 });
      }
      
    });
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}
