import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {

  public post_data;
  public form_check;
  public form_error;
  public pass_check;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService) {
    this.post_data={"password":"","new_password":"","new_password_confirm":""};
    this.form_check={"password":true,"new_password":true,"new_password_confirm":true};
    this.form_error={"password":"","new_password":"","new_password_confirm":""};
    this.pass_check={"password":false,"new_password":false,"new_password_confirm":false};
  }

  ngOnInit() {
    
  }
  
  //
  reset_password(){
    this.accountService.api_changePassword(this.post_data).then(res => {
      if(res.status){
        this.auth.get_userinfo();
        this.auth.getUserinfo_flag.subscribe(res => {
          this.router.navigateByUrl("/account/logon");
        });
      }else{
        this.form_check.password=false;
        this.pass_check.password=false;
        this.form_error['password']=res.msg;
      }
    });
  }
  
  //
  check(password_type){
    if(password_type=='password'){
      this.check_password();
    }
    
    if(password_type=='new_password'){
      this.check_new_password();
    }
    
    if(password_type=='new_password_confirm'){
      this.check_new_password_confirm();
    }
  }

  check_password(){
    this.accountService.api_check_password({"password":this.post_data['password']}).then(res => {
      if(res.status){
        this.form_check.password=true;
        this.pass_check.password=true;
        this.form_error['password']="";
      }else{
        this.form_check.password=false;
        this.pass_check.password=false;
        this.form_error['password']=res.msg;
      }
    });
  }
  
  check_new_password(){
    this.accountService.api_check_password({"password":this.post_data['new_password']}).then(res => {
      if(res.status){
        this.form_check.new_password=true;
        this.pass_check.new_password=true;
        this.form_error['new_password']="";
        
        if(this.post_data.new_password_confirm!=""){
          this.check_new_password_confirm();
        }
        
      }else{
        this.form_check.new_password=false;
        this.pass_check.new_password=false;
        this.form_error['new_password']=res.msg;
      }
    });
  }
  
  check_new_password_confirm(){
    this.accountService.api_check_password_confirm({"new_password":this.post_data.new_password,"new_password_confirm":this.post_data['new_password_confirm']}).then(res => {
      if(res.status){
        this.form_check.new_password_confirm=true;
        this.pass_check.new_password_confirm=true;
        this.form_error['new_password_confirm']="";
      }else{
        this.form_check.new_password_confirm=false;
        this.pass_check.new_password_confirm=false;
        this.form_error['new_password_confirm']=res.msg;
      }
    });
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}
